* {
  margin: 0;
}

.dody-text {
  font-family: Roboto, sans-serif;
  color: #060b35;
  font-weight: 500;
}

.card {

  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 32px 16px 16px;
  width: 166px;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
}
